@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

body {
    margin: 0;
    padding: 0;
    font-size: 1.5rem;
}

.container {
    height: 98vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.color-input {
    width: 300px;
    height: 60px;
    font-size: 1.5rem;
    font-family: "Roboto", sans-serif;
    text-align: center;
    outline: none;
    box-sizing: border-box;
}

.converted-value {
    font-family: "Roboto", sans-serif;
    width: 300px;
    height: 66px;
    background-color: rgba(204, 204, 204, 0.3);
    margin-top: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
}

input:focus::-webkit-input-placeholder {
    color: transparent;
}
